<template>
  <div class="message">
    <PersonalHeader></PersonalHeader>
    <div class="wrapper clearfix content">
      <PersonalMenu class="fl" currentMenu="1"></PersonalMenu>
      <div class="fr right">
        <div class="header" @click="tabChange">
          <div class="tab fl" data-type="2" :class="[type == 2? 'currentTab':'']">评论和赞</div>
          <div class="tab fl " data-type="1" :class="[type == 1? 'currentTab':'']">推送消息</div>
        </div>
        <div class="consult">
          <div class="consultItem clearfix" v-if="type == 2" v-for="item in data" :key="'consult'+item.id">
            <img class="avatar fl" :src="item.img" alt="">
            <div class="info fl">
              <div class="name">
                {{item.username}}
                <div class="time fr">
                  <span class="">{{item.addtime}}</span>
<!--                  todo 添加评论链接 应该有两种评论 文章和项目-->
                  <router-link to="/" class="replyBtn">查看</router-link>
                </div>
              </div>
              <div>
                {{item.content}}
              </div>
              <div class="reply">
                <div v-for="item in item.uerOper" :key="'oper'+item.id">{{item.name}}：{{item.content}}</div>
              </div>
            </div>
          </div>
          <div class="consultItem box2 clearfix" v-if="type == 1" v-for="(item,index) in data" :key="'message'+item.id">
            <div class="info fl">
              <div class="name">
                姓名 {{item.name}}
                <span class="lockBtn" :data-id="item.id"  @click="detail">查看</span>
                <div class="">手机号：{{item.phone}}</div>
              </div>
              <div class="des">
                {{item.content}}
              </div>
            </div>
          </div>
        </div>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="count"
          class="pages"
        >
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="消息通知"
      :visible.sync="centerDialogVisible"
      width="30%"
      center >
      <div class="dialog">
        <div class="head">{{currentContent.title}}</div>
        <div class="time">{{currentContent.addtime}}</div>
        <el-input type="textarea" class="content">
          {{currentContent.content}}
        </el-input>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'

  export default {
    name: "message",
    data(){
      return {
        centerDialogVisible: false,
        type: '1', //1消息 2 评论
        page: 0,
        count: 0,
        data:[],
        currentContent:{}
      }
    },
    created(){
      this.getData();
    },
    methods:{
      getData(){
        const {page,type } = this;
        if( type == '1'){
          api.getMessageList({page}).then( data =>{
            this.data = data.data;
            console.log("=====",data.data);
            this.count = data.count;
          });
        }else if(type == '2'){
          api.getConsultList({page}).then( data =>{
            this.data = data.data;
            console.log("=====",data.data);
            this.count = data.count;
          });
        }

      },

      tabChange: function({target: {dataset:{type}}}){
        this.page=0;
        this.type = type;
        this.data = [];
        this.getData();
      },
      detail: function({currentTarget: {dataset:{id}}}){
        // this.currentContent = this.data[index];
        // this.centerDialogVisible = true

        api.getDetail({id}).then(data=>{
            this.$router.push("/investorManage");
        })

      },
      PhonePwd :function(pwd){
        // # 数字0-9按从小到大顺序变成 HEBIDAGJMZN，替换完成后在第5个字符后面加JMZN 例：EZDDBKAFBBAGEM
        console.log("pwd",pwd);
        const reg = "HEBIDAGJMZN";

        let data = pwd.replace("JMZN",'');
        let phone = '';
        console.log("data",data);
        for(const item in data){
          phone += reg.indexOf(data.charAt(item));
        }
        phone = phone.substr(0,3) + "*****"+ phone.substr(-3);
        return phone
      }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .message {
    background-color: #F4F5FA;
    padding-bottom: 120px;

    .content {
      padding-top: 20px;

      .right {
        width: 950px;
        background-color: #fff;

        .header {
          height: 60px;
          line-height: 60px;
          color: #999;
          font-size: 16px;
          padding: 0 30px;

          .tab {
            margin-right: 45px;
            cursor: pointer;
          }

          .currentTab {
            color: #333;
            font-weight: bold;
            border-bottom: 2px solid #579EFA;
          }
        }

        .consult {
          padding: 0 41px;

          .consultItem {
            padding: 30px 0;
            border-bottom: 1px solid #E3E4E5;

            .avatar {
              width: 56px;
              height: 56px;
              border-radius: 50%;
              margin-right: 27px;
            }

            .info {
              width: 730px;
              font-size: 14px;
              line-height: 30px;
              color: #999;
              .des {
                padding: 10px;
                background-color: #e3e4e5;
                border-radius: 5px;
              }
              .lockBtn{
                float: right;
                color: #5389F7;
                text-decoration: underline;
                margin-left: 10px;
                cursor: pointer;
              }
              .name {
                color: #333;
                font-weight: bold;
                font-size: 16px;

                .praise {
                  font-size: 14px;
                  color: #5389F7
                }

                .noPraise {
                  font-size: 14px;
                  color: #C8C9CC
                }
              }

              .reply {
                color: #808080;
                padding: 9px 23px;
                background-color: #F4F5FA;
                margin-top: 9px;
              }

              .time {
                color: #999;
                font-weight: normal;
                .replyBtn {
                  color: #5389F7;
                  text-decoration: underline;
                  margin-left: 10px;
                }
              }
            }
          }
          .box2{
            .info{
              width: 100%;
            }
          }

          .consultItem:last-child {
            border: 0;
          }

        }
      }
    }
    /* 消息通知 */
    .dialog{
      font-size: 14px;
      line-height: 36px;
      overflow-y: auto;
      height: 300px;
      .head{
        color: #000;
        font-size: 16px;
        font-weight: bold;
      }
      .time{
        color: #999;
      }
      .content{
        color: #333333;
        padding:0;

      }
    }
    .pages{
      text-align: center;
      margin: 34px 0;
    }
  }
</style>



